body{
  @apply bg-yellow-50 dark:bg-stone-900
 }
 *{
  margin:0%;
  @apply box-border text-clrgreen
 }
 .Alquran > .container ,.Alquran > .container > .ARROW {
@apply nadu bg-transparent border-[3px] border-clrgreen rounded-[4px] font-bold 
 }

.nadu{
  @apply -translate-x-1/2 -translate-y-1/2
 }
.Alquran > form{
  @apply absolute flex items-center nadu md:right-8 md:top-8 right-2 top-6 md:bottom-0 w-32 h-6 border-[3px] border-clrgreen 
  text-xs font-semibold
}